import React, { Component } from 'react';
import Joyride, { EVENTS } from 'react-joyride';
import userGuide, { USER_GUIDES } from 'services/userGuide';
import api from 'services/api';


const locale = {
  back: 'ارجع',
  close: 'اغلق',
  last: 'تم',
  next: 'التالي',
  skip: 'تخطى'
};

const defaultStyles = {
  options: {
    arrowColor: '#E9C369',
    primaryColor: '#E9C369',
    textColor: '#0d1729',
    beaconSize: 50
  },
  tooltipContent: {
    lineHeight: '1.7rem'
  }
};


const handleGuideTourCallback = (guideKey, { type }) => {
  if (type === EVENTS.TOUR_END)
    userGuide.completeGuide(guideKey);
};


class GuideTour extends Component {
  constructor(props) {
    super(props);

    this.state = {
      run: false
    };
  }

  componentDidMount() {
    const {guideKey} = this.props;
    const isGuideFinished = userGuide.isGuideFinished(guideKey);

    if (isGuideFinished === null) {
      api.isUserGuideCompleted(guideKey)
        .then(rsp => {
          if (rsp.data.completed) {
            userGuide.completeGuide(guideKey, false);
            this.setState({...this.state, run: false});
          } else {
            userGuide.incompleteGuide(guideKey, false);
            this.setState({ ...this.state, run: true });
          }
        });
    } else {
      this.setState({...this.state, run: !isGuideFinished});
    }
  }

  handleCallback = data => handleGuideTourCallback(this.props.guideKey, data);

  render () {
    const { guideKey, styles } = this.props;
    const { run } = this.state;

    const steps = USER_GUIDES[guideKey].steps;
    const isMoreThanOneStep = steps.length > 1;

    return (
      <Joyride
        run={run}
        steps={steps}
        locale={locale}
        callback={this.handleCallback}
        showProgress={true}
        spotlightClicks={true}
        continuous={isMoreThanOneStep}
        showSkipButton={isMoreThanOneStep}
        disableCloseOnEsc={true}
        disableOverlayClose={true}
        disableScrollParentFix={true}
        {...this.props}
        styles={{...defaultStyles, ...(styles || {})}}
      />
    );
  }
}

export default GuideTour;
