class UserGuide {
  STORAGE_KEY = 'mofkra_storage_user_guide';

  completeGuide = (guideKey, syncWithBackend = true) => {
    let currentUser = session.currentUser();
    if (!currentUser)
      return;

    const guideID = USER_GUIDES[guideKey].id;

    let key = `${this.STORAGE_KEY}_${guideID}_${currentUser.username}`;

    if (syncWithBackend) {
      api.completeUserGuide(guideKey)
        .then(() => localStorage.setItem(key, JSON.stringify(true)));
    } else {
      localStorage.setItem(key, JSON.stringify(true));
    }
  };

  incompleteGuide = (guideKey, syncWithBackend = true) => {
    let currentUser = session.currentUser();
    if (!currentUser)
      return;

    const guideID = USER_GUIDES[guideKey].id;

    let key = `${this.STORAGE_KEY}_${guideID}_${currentUser.username}`;

    if (syncWithBackend) {
      api.incompleteUserGuide(guideKey)
        .then(() => localStorage.setItem(key, JSON.stringify(false)));
    } else {
      localStorage.setItem(key, JSON.stringify(false));
    }
  };

  isGuideFinished = guideKey => {
    let currentUser = session.currentUser();
    if (!currentUser)
      return false;

    const guideID = USER_GUIDES[guideKey].id;

    let key = `${this.STORAGE_KEY}_${guideID}_${currentUser.username}`;
    return JSON.parse(localStorage.getItem(key));
  };
}

export default new UserGuide();
